


























import Vue from 'vue';

export default Vue.extend({
  name: 'Install',

  data: () => ({
    navigator,
    window,
  }),

  computed: {
    dialog: {
      get () {
        return this.$store.getters['dialogs/install'];
      },
      set (value: boolean) {
        this.$store.commit('dialogs/install', value);
      }
    }
  }
})
